    flex-direction: column;
    justify-content: space-around;
  }
  .cajaDeComentarios{
    height: 60vh;
    width: 100vw;
  }
  .enviarComentario{
    width: 100vw;
  }
  .enviarComentario > input{
    width: 60vw;
  }
  .inputComentar > input{
